                    </div>
                </router-link>
                <router-link to="/Cart" class="car block">
                    <i id="car_icon" class="block"></i>
                    <span class="cart_count" v-text="getCount"></span>
                    <div  class="box cart_box">
                        <i class="triangle cart_tri"></i>
                        <p v-if="getCount==0" class="cart_empty"></p>
                        <div v-else>
